// MODULES
import {BrowserModule} from "@angular/platform-browser";
import {NgModule} from "@angular/core";
import {FormsModule} from "@angular/forms";
import {HttpModule} from "@angular/http";
import {RouterModule} from "@angular/router";
// COMPONENTS
import {AppComponent} from "./app.component";
import {PokemonComponent} from "./index";
import {PokemonListComponent} from "./pokemon-list/pokemon-list.component";
import {PokemonFormComponent} from "./pokemon-form/pokemon-form.component";
import {PokemonBoxComponent} from "./pokemon-box/pokemon-box.component";
import {TrainerListComponent} from "./trainer-list/trainer-list.component";
import {TrainerFormComponent} from "./trainer-form/trainer-form.component";
import {TrainerBoxComponent} from "./trainer-box/trainer-box.component";
// SERVICES
import {EmitterServiceService} from "./emitter-service.service";

@NgModule({
  imports: [
    BrowserModule,
    FormsModule,
    HttpModule,
    RouterModule.forRoot(  [{
          path: 'pokemon',
          component: PokemonListComponent,
        },
        {
          path: 'trainers',
          component: TrainerListComponent,
        }])
],
  declarations: [
    AppComponent,
    PokemonFormComponent,
    PokemonListComponent,
    PokemonBoxComponent,
    PokemonComponent,
    TrainerListComponent,
    TrainerBoxComponent,
    TrainerFormComponent,
  ],
  providers: [
    EmitterServiceService,
  ],
  bootstrap: [
    AppComponent
  ],
  exports:[
    AppComponent,
    PokemonFormComponent,
    PokemonListComponent,
    PokemonBoxComponent,
    PokemonComponent,
    TrainerListComponent,
    TrainerBoxComponent,
    TrainerFormComponent,
  ]
})
export class AppModule { }



