import {Component, Input, OnInit, SimpleChanges} from "@angular/core";
// SERVICE
import {EmitterServiceService} from "../emitter-service.service";
// MODEL
import {Trainer} from "../model/Trainer";
import {OnChanges} from "angular2/core";

@Component({
  selector: 'app-trainer-box',
  templateUrl: './trainer-box.component.html',
  // styleUrls: ['./pokemon-box.component.css']
})

export class TrainerBoxComponent implements OnChanges{

  // Constructor
  constructor(
      private EmitterServiceService: EmitterServiceService
  ){}
  // Define input properties
  @Input() trainer: Trainer;
  @Input() trainerlistId: string;
  @Input() trainereditId: string;

  editTrainer(){
    // Emit edit event
    EmitterServiceService.get(this.trainereditId).emit(this.trainer);
  }

  deleteTrainer(id:string){
    // Call removeComment() from CommentService to delete comment
    this.EmitterServiceService.removeTrainer(id).subscribe(
        trainerz => {
          EmitterServiceService.get(this.trainerlistId).emit(trainerz);
        },
        err => { console.log(err); });
  }
  ngOnChanges(){
    // this.EmitterServiceService.getPokemon();
    console.log("init trainer box")
  }
  //Deze gaat af bij het inladen van de box-component
  // ngOnChanges(changes:SimpleChanges){
  //   console.log("trainer box");
  // }

}



